import { Time } from '@angular/common';
import { Component, OnInit } from '@angular/core';
import { interval, Subscriber } from 'rxjs';
import {DataService} from '../data.service';
declare const confetti: any;
@Component({
  selector: 'hangman',
  templateUrl: './hangman.component.html',
  styleUrls: ['./hangman.component.css']
})
export class HangmanComponent implements OnInit {

  vowel: string;
  const: string;
  used: Array<string>;
  words: Array<any>;
  word: Array<object>;
  ctr: number;
  px: Array<string>;
  img: Array<string>;
  playing: boolean;
  animate: boolean;
  timers: Array<any>;
  time: number;

  constructor(private api: DataService){
    this.const = 'bcdfghjklmnpqrstvwxyz';
    this.vowel = 'aeiou';
    this.playing = false;
    this.words = [];
    this.word = null;
    this.used = [];
    this.ctr = 6;
    this.animate = false;
    this.timers = []
    this.time = 0
    this.px = ['0px', '10px', '20px', '25px', '30px', '35px', '40px'];
    this.img = [
      '/assets/six.png',
      '/assets/five.png',
      '/assets/four.png',
      '/assets/three.png',
      '/assets/two.png',
      '/assets/one.png',
      '/assets/zero.png',
    ];
  }
  ngOnInit(): void{
    this.api.getWord().subscribe(( data ) => {
      //this.word = data['words'][Math.floor(Math.random() * this.words.length) + 1 ];
      this.words = data['words'];
      console.log(data)
    });
  
    this.startGame();
  }
  reset(): void{
    
    this.ctr = 6;
    this.vowel = 'aeiou';
    this.const = 'bcdfghjklmnpqrstvwxyz';
    this.placeWord();
    this.updateNoose();
    this.updateImage();
    this.playing = true;
    this.used = [];
    this.animate = false;
    this.time = 0
    this.timer()
  }
  resetScreen(): void{
    document.getElementById('startGame').classList.add("hidden")
  }
  resetTime(): void{
    document.getElementById('timer').innerText = "0"
  }
  timer(): void{
    this.timers.push(setInterval(()=>{
      this.time +=1
      let timer = document.getElementById('timer')
      timer.innerText = (this.time/1000).toString()
    },1))
  }
  placeWord(): void{
    this.word = this.words[Math.floor(Math.random() * this.words.length) + 1 ];
    console.log(this.word)
  }
  updateConst(ltr: string): void{
    this.const = this.const.replace(ltr, '');
  }
  updateVowel(ltr: string): void{
    this.vowel = this.vowel.replace(ltr, '');
  }
  updateNoose(): void{
    document.getElementById('noose').style.top = this.px[this.ctr ];
  }
  updateImage(): void{
    (<HTMLImageElement>document.querySelector('#imgFace')).src = this.img[this.ctr];
  }
  counter(): void{
    this.ctr -= 1;
  }
  gameStep(): void{
    this.counter();
    this.updateNoose();
    this.updateImage();
  }
  hasLetter(ltr: string): boolean{
    let found = false;
    this.word.forEach( (arg, i) => {
      if( arg['letter'].toLowerCase() === ltr.toLowerCase()){
        this.word[i]['visible'] = true;
        found = true;
      }
    });
    return found;
  }
  updateWord(ltr: string): void{
    this.hasLetter(ltr) ? null : this.gameStep();
  }
  showLetters(): void{
    this.word.forEach((ltr, i) => this.word[i]['visible'] = true );
  }
  updateLetters(letter: string): void{

    this.updateConst(letter);
    this.updateVowel(letter);
    this.updateWord(letter);
    this.used.push(letter);
  }

  getLetter(e): string{
    const code = (e.keyCode ? e.keyCode : e.which);
    return String.fromCharCode(code).toLowerCase();
  }
  validate(letter: string): boolean{
    const validLetter = this.vowel.split('').includes(letter) || this.const.split('').includes(letter);
    const notUsed = this.playing && !this.used.includes(letter);
    return validLetter && notUsed ? true : false;
  }
  checkLoss(): boolean{
    const lost =  this.ctr === 0 ? true : false;
    if(lost){
      console.log('looser')
      this.resetIntervalTimers()
    }
    return lost; 
  }
  resetIntervalTimers(): void{
    if(this.timers.length){
      this.timers.forEach(t=>{
        clearInterval(t); 
        this.timers = [];
      });
    }
  }
  checkWin(): boolean{
    let done = true;
    this.word.forEach(word => word['visible'] === false ? done = false : null);
    if(this.ctr > 0 && done){
      console.log('winner')
      this.resetIntervalTimers()
      this.animate = true
      confetti.start(3000)
      let face = document.getElementById("face");
      let noose = document.getElementById("noose");
      face.classList.add("rotate")
      noose.classList.add("rotate")
      setTimeout(()=>{face.classList.remove("rotate")}, 3000)
      setTimeout(()=>{noose.classList.remove("rotate")}, 3000)
    }
    return done; 
  }
  startGame(): void{
    window.onkeypress = e => {
      const letter = this.getLetter(e);
      this.validate(letter) ? this.updateLetters(letter) : null;
      this.checkLoss() ? this.showLetters() : null;
      this.checkWin() ? this.playing = false : 'null';
    };
  }
}
